video {
    position: fixed;
    right: 0;
    bottom: 0;
    min-width: 100%;
    min-height: 100%;
    z-index: -1;
    filter: blur(3px) opacity(50%);
}

.grid-display {
    display: grid;
    grid-template-columns: 0fr 2fr;
    grid-template-rows: 92px 2fr 2fr 2fr 2fr 2fr 1fr;
    grid-template-areas:
        "header header"
        "sidebar page"
        "sidebar page"
        "sidebar page"
        "sidebar page"
        "sidebar page"
        "sidebar page";
    width: 100vw;
    height: 100vh;
}

.header {
    grid-area: header;
}

.page {
    grid-area: page;
}
