@tailwind base;
@tailwind components;
@tailwind utilities;

a {
    text-shadow: 2px 2px #212121;
    letter-spacing: 0.1rem;
}

.customSize {
    font-size: 42px;
    margin-right: 10px;
}

@layer components {
    a {
        font-family: "Lexend", sans-serif !important;
        @apply relative inline-block mt-8 text-lg text-white;
    }
    a:after {
        content: "";
        height: 2px;
        @apply block absolute left-[50%] bg-white w-0;
        transition: width 0.3s ease 0s, left 0.3s ease 0s;
    }
    a:hover:after {
        @apply w-full left-0;
    }
}
